ul{ list-style-type: none; margin:0; padding:0; }
a{ text-decoration: none; }
img{ display:block; border:0 none; }


#header_section{
    height:44px; 
    background-color:#fff;
    position:fixed; 
    left:0; 
    right:0; 
    top:0;
    z-index:99999;
    border-bottom:1px solid #ccc; 
    #nav{
        max-width:980px; 
        margin:0 auto; 
        text-align:center;
        ul{
            height:44px;
            display:flex;
            justify-content: center;
            align-items: center;
            gap:10px; 
            a{
                display:block;  
                line-height: 14px;
                padding:6px 24px; 
                font-size:14px; 
                letter-spacing: 0.1;
                font-weight:bold;
                color:#222;
                &:hover{
                    color:#fc0;
                    text-decoration: underline;
                }
            }
        }
    }
}
#main_content{ 
    .main_visual{ 
        width:100%; 
        height:774px; 
        .inner{
            max-width:1920px; 
            width:100%;
            height:100%;
            margin:0 auto; 
            img{
                width:100%;
                height:100%; 
                object-fit: cover;
            }
        }
    }
    .title{
        font-size:18px; 
        border-bottom:1px dotted #d9d9d9;
        margin-bottom:10px;
        padding-bottom:10px;
    }
    #contents{ 
        width:100%; 
        max-width:980px; 
        margin:0 auto;
        .now_movies{
            margin:40px 0 60px; 
            .movies_wrap{
                padding-top:20px; 
                display:flex; 
                flex-flow:row wrap;
                justify-content:space-between; 
                gap:15px; 
                li{
                    flex:1 1 0; 
                    img{ width:100%; height:auto; object-fit:cover;}
                }
                
            }
            
        }
        .special{
            margin-bottom:34px; 
            .sp_wrap{
                display:flex; 
                justify-content:space-between;
                padding:16px 0;
                /* flex-flow: row wrap; overflow:hidden; */
            }
        }
        .evt{ 
            margin-bottom:34px;
            .evt_wrap{ 
                height:512px; 
                display:flex; 
                flex-flow:row wrap; 
                gap:11px; 
                position:relative;
                li{ 
                    height:250px; 
                }
                .e1{ width:183px; }
                .e2{ width:183px; }
                .e3{ width:286px; }
                .e4{ width:295px; height:511px; position:absolute; right:0; top:0; }
                .e5{ width:478px; }
                .e6{ width:183px; }
            }
            /*
            .evt_wrap{ 
                height:512px; 
                display:grid; 
                grid-template-columns: 183px 183px 286px 295px 478px 183px;
                grid-template-rows: 250px 250px;
            }
            */
        }
        .direct_btns{
            margin-bottom:150px; 
            display:flex; 
            justify-content: space-between; 
        }
    }
}
#evt_box{ 
    padding-top:100px; 
    display:flex;
    justify-content: center;
}